
import { useState, useEffect, useRef } from 'react';
import { Player, PlayerType, GamePhase, CardRank, SupportedLanguage } from '../types';
import { getAIMove, generateSpeech } from '../services/geminiService';
import { getAIChatMessage, ChatContext } from '../services/aiChatService';
import { getAIState } from '../services/aiLearningService';

interface UseAIAgentProps {
    gameState: any; 
    gameActions: any;
    audioSystem: any;
    isMuted: boolean;
    language: SupportedLanguage;
}

export const useAIAgent = ({ gameState, gameActions, audioSystem, isMuted, language }: UseAIAgentProps) => {
    const [aiChatMessages, setAiChatMessages] = useState<Record<number, string | null>>({});
    const processingRef = useRef(false);
    
    // --- SILENCE & CHAT CONTROL REFS ---
    const nextChatAvailableTime = useRef<number>(0);
    const lastSpeechTime = useRef<number>(0);

    const { 
        players, currentPlayerIndex, phase, trickNumber, tableCards, 
        recordedCards, bankerId, difficulty, gameHistory, openedSuits, 
        firstLeadInfo, mianZhangCard, lastGameEvent, trickWinnerId
    } = gameState;

    // 1. AI MOVEMENT LOGIC (Unchanged)
    useEffect(() => {
        const currentPlayer = players[currentPlayerIndex];
        
        const shouldMove = 
            phase === GamePhase.PLAYING && 
            trickNumber < 8 && 
            currentPlayer?.type === PlayerType.AI && 
            tableCards.length < 4 && 
            !trickWinnerId &&
            !processingRef.current;

        if (shouldMove) {
            processingRef.current = true;
            const timer = setTimeout(async () => {
                const aiPlayer = currentPlayer;
                const tableCardsForAI = tableCards.map((t: any) => ({card: t.card, playerId: t.playerId}));
                
                try {
                    const move = await getAIMove(
                        aiPlayer, tableCardsForAI, players, recordedCards, bankerId, 
                        difficulty, gameHistory, openedSuits, firstLeadInfo, trickNumber, mianZhangCard
                    );
                    
                    if (move) { 
                        // Trigger speech for special moves - CHECK COOLDOWN FIRST
                        if ((move.rank === CardRank.ZUN || move.rank === CardRank.BAI) && canSpeak()) {
                            // High card play is a high priority event, might override small cooldowns? 
                            // No, strict adherence to Zen silence.
                            executeConversation(aiPlayer.id, 'PLAY_HIGH_CARD');
                        }
                        await gameActions.executePlayCard(aiPlayer.id, move.id); 
                    }
                } catch (e) {
                    console.error("AI Move Error", e);
                } finally {
                    processingRef.current = false;
                }
            }, 1000 + Math.random() * 500); 
            return () => clearTimeout(timer);
        }
    }, [currentPlayerIndex, phase, tableCards.length, trickWinnerId, trickNumber]);

    // 2. AI REACTION LOGIC (Chat & Voice)
    useEffect(() => {
        if (!lastGameEvent) return;

        const handleEvent = async () => {
            // Early exit if global silence is active
            if (!canSpeak()) return;

            const { type, playerId, context } = lastGameEvent;
            const player = players.find((p: Player) => p.id === playerId);
            
            // Conversation Starter Logic
            let primarySpeakerId: number | null = null;
            let chatContext: ChatContext | null = null;

            if (type === 'TRICK_WIN' && player?.type === PlayerType.AI) {
                primarySpeakerId = playerId;
                chatContext = context?.isBig ? 'TRICK_WIN_BIG' : 'TRICK_WIN';
            } else if (type === 'VIOLATION') {
                // Teacher speaks
                const teacher = players.find((p: Player) => p.type === PlayerType.AI);
                if (teacher) {
                    primarySpeakerId = teacher.id;
                    chatContext = 'TEACH_VIOLATION';
                }
            } else if (type === 'KAI_CHONG_SUCCESS' && player?.type === PlayerType.AI) {
                primarySpeakerId = playerId;
                chatContext = 'KAI_CHONG_SUCCESS';
            } else if (type === 'KAI_CHONG_FAIL' && player?.type === PlayerType.AI) {
                primarySpeakerId = playerId;
                chatContext = 'KAI_CHONG_FAIL';
            }

            if (primarySpeakerId !== null && chatContext !== null) {
                executeConversation(primarySpeakerId, chatContext);
            }
        };

        handleEvent();

    }, [lastGameEvent]);

    // Check if we are allowed to break silence
    const canSpeak = (): boolean => {
        const now = Date.now();
        return now > nextChatAvailableTime.current && now > lastSpeechTime.current + 5000;
    };

    // Orchestrates a "Conversation" (1 or 2 people) and sets the cooldown
    const executeConversation = (speakerId: number, context: ChatContext) => {
        // 1. Primary Speaker speaks immediately
        speak(speakerId, context);

        // 2. Determine if a Responder exists (30% chance, max 2 people)
        // Only if it's not a generic move, maybe reaction to win/loss
        const shouldRespond = Math.random() < 0.3;
        
        let conversationDuration = 4000; // Base duration for 1 sentence

        if (shouldRespond) {
            const responders = players.filter((p: Player) => p.id !== speakerId && p.type === PlayerType.AI);
            if (responders.length > 0) {
                const responder = responders[Math.floor(Math.random() * responders.length)];
                
                // Determine response context
                let responseContext: ChatContext | null = null;
                if (context === 'TRICK_WIN' || context === 'TRICK_WIN_BIG') responseContext = 'TRICK_LOSS'; // Loser laments
                else if (context === 'KAI_CHONG_SUCCESS') responseContext = 'KAI_CHONG_FAIL'; // Envy? or just generic waiting
                
                if (responseContext) {
                    // Schedule Response with 5s delay minimum as requested
                    setTimeout(() => {
                        speak(responder.id, responseContext!);
                    }, 5500);
                    conversationDuration += 6000; 
                }
            }
        }

        // 3. Set Global Cooldown (15-60 seconds) AFTER this conversation finishes
        const silenceDuration = 15000 + Math.random() * 45000;
        nextChatAvailableTime.current = Date.now() + conversationDuration + silenceDuration;
        
        console.log(`[AI Chat] Conversation started. Next chat allowed in ${(conversationDuration + silenceDuration)/1000}s`);
    };

    const speak = async (playerId: number, context: ChatContext) => {
        // Update last speech time to prevent overlap
        lastSpeechTime.current = Date.now();

        const player = players.find((p: Player) => p.id === playerId);
        if (!player) return;

        const learningState = getAIState(playerId, gameHistory || {});
        const message = getAIChatMessage(context, language, learningState);
        
        if (message) {
            // Visual Chat Bubble
            setAiChatMessages(prev => ({ ...prev, [playerId]: message }));
            setTimeout(() => { setAiChatMessages(prev => ({ ...prev, [playerId]: null })); }, 4000);
            
            // Audio TTS
            if (!isMuted) {
                let voice = 'Puck';
                if (playerId === 2) voice = 'Kore';
                if (playerId === 3) voice = 'Fenrir';
                // Try catch to prevent logic break if audio fails
                try {
                    const pcmData = await generateSpeech(message, voice);
                    if (pcmData) audioSystem.playPcmAudio(pcmData);
                } catch (e) {
                    console.warn("TTS Failed", e);
                }
            }
        }
    };

    return {
        aiChatMessages
    };
};
