
import React, { useState, useRef, useEffect, useMemo } from 'react';
import { Card, GameHistory, Suit, ScoreResult, SupportedLanguage, Player, PatternDetail, KaiChongDetail } from '../../types';
import CardComponent from '../Card';
import { initSupabase, uploadAvatar, getSupabaseConfig, clearSupabaseConfig } from '../../services/cloudService';
import { useSkin } from '../../contexts/SkinContext';
import { AppIcon } from '../Visuals/AppIcon';
import { SettingsTab } from '../../hooks/useUIState';
import { TRANSLATIONS } from '../../constants';
import { useGameSettings, GameSettings } from '../../hooks/useGameSettings';

// --- HELPERS ---

const truncateName = (name: string) => {
    return name.length > 8 ? name.substring(0, 8) + '...' : name;
};

const getFlagUrl = (countryCode: string | undefined) => {
    const code = countryCode ? countryCode.toLowerCase() : 'cn'; 
    return `https://flagcdn.com/w40/${code}.png`;
};

// Richer Gradients for Text
const getScoreGradientClass = (val: number) => {
    if (val < 0) return 'text-transparent bg-clip-text bg-gradient-to-b from-rose-400 to-red-600 drop-shadow-sm font-black'; 
    if (val > 0) return 'text-transparent bg-clip-text bg-gradient-to-b from-emerald-300 to-emerald-600 drop-shadow-[0_0_8px_rgba(16,185,129,0.4)] font-black'; 
    return 'text-stone-500 font-bold'; 
};

const getDiaoBadge = (trickCount: number) => {
    if (trickCount < 2) return { text: "", bg: "bg-red-900/40", border: "border-red-800", textCol: "text-red-300" };
    if (trickCount === 2) return { text: "", bg: "bg-stone-800/40", border: "border-stone-700", textCol: "text-stone-400" };
    // Updated Logic: Anything > 2 is 'De Diao' (Gain Diao), no 'Double Diao'
    return { text: "", bg: "bg-emerald-900/40", border: "border-emerald-700", textCol: "text-emerald-300" };
};

const CardZoomOverlay = ({ card, onClose }: { card: Card | null, onClose: () => void }) => {
    if (!card) return null;
    return (
        <div className="fixed inset-0 z-[1000] flex items-center justify-center bg-black/90 backdrop-blur-lg animate-fade-in cursor-zoom-out" onClick={onClose}>
            <div className="relative transform scale-[1.5] md:scale-[2.0] transition-transform duration-300 shadow-[0_0_50px_rgba(0,0,0,1)] rounded-xl" onClick={(e) => e.stopPropagation()}>
                <div className="w-40 h-64 pointer-events-none">
                    <CardComponent card={card} isTrick={false} isHand={false} isSmall={false} isFaceDown={false} isInverted={card.isPot} />
                </div>
            </div>
            <div className="absolute bottom-10 text-white/50 text-xs md:text-sm font-serif tracking-[0.3em] animate-pulse">
                TAP TO CLOSE
            </div>
        </div>
    );
};

// Unified Button Component with Shine
const ModalButton = ({ onClick, label, primary = false, icon = null }: any) => (
    <button
        onClick={onClick}
        className={`
            relative overflow-hidden group
            px-8 py-4 min-w-[160px] md:min-w-[200px]
            font-serif font-bold tracking-[0.25em] text-sm md:text-base uppercase
            transition-all duration-300 rounded-[4px]
            flex items-center justify-center gap-2
            shadow-[0_4px_15px_rgba(0,0,0,0.5)]
            ${primary 
                ? 'bg-gradient-to-b from-[#e6c278] to-[#b48a26] text-[#1a0505] border border-[#fcd34d]/50 shadow-[0_0_20px_rgba(212,175,55,0.2)]' 
                : 'bg-black/60 border border-[#5c4025] text-[#8c6239] hover:text-[#d4af37] hover:border-[#d4af37] backdrop-blur-md'}
        `}
    >
        {/* Shine Sweep */}
        <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-700 ease-in-out pointer-events-none"></div>
        
        <span className="relative z-10 drop-shadow-md">{label}</span>
        {icon && <span className="relative z-10 text-lg">{icon}</span>}
    </button>
);

interface ScoreRowProps {
    label: string;
    score: number;
    cards: Card[];
    highlightInverted?: boolean; 
    onCardClick: (card: Card) => void;
}

const ScoreRow: React.FC<ScoreRowProps> = React.memo(({ label, score, cards, highlightInverted, onCardClick }) => (
    <div className="flex flex-col md:flex-row items-start md:items-center justify-between border-b border-[#3e2b22]/30 p-4 hover:bg-white/5 transition-colors gap-3 font-serif bg-[#0f0a08]/40 rounded-sm mb-1 group relative overflow-hidden">
        {/* Hover Highlight */}
        <div className="absolute inset-0 bg-gradient-to-r from-[#d4af37]/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none"></div>
        
        <div className="w-full md:w-1/3 flex items-center gap-2 relative z-10">
            <div className="w-1 h-4 bg-[#5c4025] group-hover:bg-[#d4af37] transition-colors"></div>
            <span className="text-[#a0a0a0] group-hover:text-[#d4af37] text-sm tracking-wider font-bold transition-colors">{label}</span>
        </div>
        <div className="flex flex-wrap gap-2 md:justify-center w-full md:w-1/3 py-2 md:py-0 relative z-10">
            {cards.map((c, i) => {
                const isInverted = c.isPot || (highlightInverted && i === cards.length - 1);
                return (
                    <div key={i} className="relative z-10 min-w-[30px] min-h-[45px] transition-transform hover:-translate-y-1 cursor-pointer" onClick={(e) => { e.stopPropagation(); onCardClick(c); }}>
                        <CardComponent card={c} isSmall isInverted={isInverted} isFaceDown={false} isLocked={true} />
                    </div>
                );
            })}
        </div>
        <div className="w-full md:w-1/3 text-right relative z-10">
            <span className={`font-black text-xl ${score > 0 ? 'text-[#4ade80] drop-shadow-[0_0_5px_rgba(74,222,128,0.5)]' : 'text-[#f87171]'}`}>
                {score > 0 ? '+' : ''}{score}
            </span>
        </div>
    </div>
));

export const ScoreModal = ({ 
    results, onClose, onNextRound, onHome, onQuit, bankerId, language, players 
}: { 
    results: ScoreResult[], 
    onClose: () => void, 
    onNextRound: () => void, 
    onHome: () => void,
    onQuit: () => void,
    bankerId: number, 
    language: SupportedLanguage, 
    players: Player[] 
}) => {
    const { skin } = useSkin(); 
    const { isMobileDevice } = useGameSettings();
    const [expandedIds, setExpandedIds] = useState<number[]>([]);
    const [zoomedCard, setZoomedCard] = useState<Card | null>(null);

    const toggleExpand = (id: number) => { setExpandedIds(prev => prev.includes(id) ? prev.filter(x => x !== id) : [...prev, id]); };
    
    // SAFE TRANSLATION LOOKUP
    const t = (key: string) => {
        const dict = TRANSLATIONS[language] || TRANSLATIONS['en'];
        return dict?.[key] || key;
    };
    
    const getKcLabel = (type: string) => { const map: Record<string, string> = { 'TONG': 'kc_single', 'BROTHER': 'kc_brother', 'SHUANG_LIN': 'kc_double', 'SHUN_LING': 'kc_seq', 'JIAN_LING': 'kc_gap' }; const key = map[type]; return key ? t(key) : type; };

    // Auto-expand the winner (or me if I won)
    useEffect(() => {
        const winner = results.reduce((prev, current) => (prev.totalRoundChange > current.totalRoundChange) ? prev : current);
        if (winner && expandedIds.length === 0) {
            setExpandedIds([winner.playerId]);
        }
    }, []);

    const RenderVisuals = ({ res }: { res: ScoreResult }) => {
        return (
            <div className="bg-[#0a0503] p-4 md:p-6 shadow-[inset_0_0_40px_rgba(0,0,0,0.9)] border-t border-[#3e2b22] animate-fade-in relative overflow-hidden">
                {/* Background Pattern */}
                <div className="absolute inset-0 opacity-10 bg-[url('https://www.transparenttextures.com/patterns/wood-pattern.png')] pointer-events-none"></div>
                
                {/* Mobile Grid Stats */}
                <div className="grid grid-cols-5 gap-2 mb-6 md:hidden text-center bg-[#15100e] p-3 rounded-sm border border-[#3e2b22]/50">
                    <div className="flex flex-col gap-1">
                        <div className="text-[8px] text-[#5c4025] uppercase tracking-wider">{t('diaoPoints')}</div>
                        <div className={`text-xs ${res.diaoPointChange !== 0 ? 'text-[#e6c278]' : 'text-[#444]'}`}>{res.diaoPointChange}</div>
                    </div>
                    <div className="flex flex-col gap-1">
                        <div className="text-[8px] text-[#5c4025] uppercase tracking-wider">{t('kaiZhuPoints')}</div>
                        <div className={`text-xs ${res.kaiZhuPointChange !== 0 ? 'text-[#e6c278]' : 'text-[#444]'}`}>{res.kaiZhuPointChange}</div>
                    </div>
                    <div className="flex flex-col gap-1">
                        <div className="text-[8px] text-[#5c4025] uppercase tracking-wider">{t('qiaoMenPoints')}</div>
                        <div className={`text-xs ${res.qiaoMenPointChange !== 0 ? 'text-[#e6c278]' : 'text-[#444]'}`}>{res.qiaoMenPointChange}</div>
                    </div>
                    <div className="flex flex-col gap-1">
                        <div className="text-[8px] text-[#5c4025] uppercase tracking-wider">{t('seYangPoints')}</div>
                        <div className={`text-xs ${res.seYangPointChange !== 0 ? 'text-[#e6c278]' : 'text-[#444]'}`}>{res.seYangPointChange}</div>
                    </div>
                    <div className="flex flex-col gap-1">
                        <div className="text-[8px] text-[#5c4025] uppercase tracking-wider">{t('kaiChongPoints')}</div>
                        <div className={`text-xs ${res.kaiChongPointChange !== 0 ? 'text-[#e6c278]' : 'text-[#444]'}`}>{res.kaiChongPointChange}</div>
                    </div>
                </div>

                {res.allCapturedCards && res.allCapturedCards.length > 0 && (
                    <div className="mb-6 relative z-10">
                        <div className="flex items-center gap-2 mb-3 border-b border-[#3e2b22] pb-2">
                            <span className="text-[#8c6239] text-xs font-bold uppercase tracking-[0.2em]">{t('visibleCards')}</span>
                            <span className="bg-[#3e2b22] text-[#d9c7b0] text-[10px] px-1.5 py-0.5 rounded-sm font-mono">{res.cardsWonCount}</span>
                        </div>
                        <div className="flex flex-wrap gap-2 pl-2">
                            {res.allCapturedCards.map((c, i) => (
                                <div key={i} className="relative z-10 min-w-[30px] min-h-[45px] transition-transform hover:-translate-y-1 duration-300 hover:scale-110 hover:z-50" onClick={(e) => { e.stopPropagation(); setZoomedCard(c); }}>
                                    <CardComponent card={c} isSmall isInverted={c.isPot} isFaceDown={false} isLocked={true} />
                                </div>
                            ))}
                        </div>
                    </div>
                )}
                
                <div className="space-y-1 relative z-10">
                    {[...res.kaiChongDetails.map(k => ({
                        name: getKcLabel(k.matchType), 
                        cards: [...k.sourceCards, k.matchedCard],
                        score: k.score,
                        isInverted: true 
                    })), ...res.patternDetails.map(p => ({
                        name: p.name, // Usually already translated if stored correctly, or needs lookup
                        cards: p.cards,
                        score: p.score,
                        isInverted: false
                    }))].map((pd: any, i) => (
                         <ScoreRow key={i} label={pd.name} score={pd.score} cards={pd.cards || []} highlightInverted={pd.isInverted} onCardClick={setZoomedCard} />
                    ))}
                    {res.patternDetails.length === 0 && res.kaiChongDetails.length === 0 && (
                        <div className="text-center text-[#3e2b22] italic py-4 text-xs border border-dashed border-[#2a1d15] rounded-sm">No special patterns formed</div>
                    )}
                </div>
            </div>
        );
    };

    return (
        <div className="fixed inset-0 z-[500] bg-[#050202] flex flex-col animate-fade-in font-serif overflow-hidden">
            {/* Improved Background Atmosphere */}
            <div className="absolute inset-0 bg-[url('https://www.transparenttextures.com/patterns/black-scales.png')] opacity-30 pointer-events-none z-0"></div>
            <div className="absolute top-[-30%] left-1/2 -translate-x-1/2 w-[150%] h-[80%] bg-[radial-gradient(circle,rgba(140,28,11,0.15)_0%,transparent_70%)] blur-[120px] pointer-events-none z-0"></div>
            <div className="absolute bottom-[-30%] right-[-20%] w-[100%] h-[60%] bg-[radial-gradient(circle,rgba(212,175,55,0.05)_0%,transparent_70%)] blur-[120px] pointer-events-none z-0"></div>

            <CardZoomOverlay card={zoomedCard} onClose={() => setZoomedCard(null)} />

            {/* Header */}
            <div className="relative z-10 flex flex-col items-center justify-center pt-8 md:pt-12 pb-6 shrink-0">
                <h2 className="text-5xl md:text-7xl font-calligraphy text-transparent bg-clip-text bg-gradient-to-b from-[#ffd700] via-[#e6c278] to-[#8c6239] drop-shadow-[0_4px_15px_rgba(0,0,0,0.9)] mb-2 animate-fade-in-up tracking-widest">
                    {t('tab_summary')}
                </h2>
                <div className="w-24 h-[2px] bg-gradient-to-r from-transparent via-[#8c6239] to-transparent opacity-70 shadow-[0_0_10px_#8c6239]"></div>
            </div>

            {/* Content Container - Full Width on Mobile, Max Width on Desktop */}
            <div className="flex-1 overflow-y-auto custom-scrollbar px-0 md:px-20 pb-20 w-full max-w-7xl mx-auto relative z-10">
                <div className="grid gap-4 px-4 pb-4">
                    {/* Desktop Headers */}
                    <div className="hidden md:grid grid-cols-12 gap-4 text-[#5c4025] text-xs font-bold uppercase tracking-[0.2em] px-6 pb-2 border-b border-[#2a1d15]">
                        <div className="col-span-4">{t('player')}</div>
                        <div className="col-span-1 text-center">{t('diaoPoints')}</div>
                        <div className="col-span-1 text-center">{t('kaiZhuPoints')}</div>
                        <div className="col-span-1 text-center">{t('qiaoMenPoints')}</div>
                        <div className="col-span-1 text-center">{t('seYangPoints')}</div>
                        <div className="col-span-1 text-center">{t('kaiChongPoints')}</div>
                        <div className="col-span-3 text-right">{t('score')}</div>
                    </div>

                    {results.map(res => {
                        const isBanker = res.playerId === bankerId;
                        const isMe = res.playerId === 0;
                        const isExpanded = expandedIds.includes(res.playerId);
                        const isWinner = res.totalRoundChange > 0;
                        const playerObj = players.find(p => p.id === res.playerId);
                        const profile = playerObj?.profile;
                        const avatarUrl = profile?.avatar_url || `https://api.dicebear.com/9.x/miniavs/svg?seed=${playerObj?.name || 'Player'}&backgroundColor=b6e3f4,c0aede,d1d4f9`;
                        const flagUrl = getFlagUrl(profile?.country || 'cn');
                        const diaoBadge = getDiaoBadge(res.trickCount);

                        return (
                            <div 
                                key={res.playerId} 
                                className={`
                                    group transition-all duration-500 rounded-md overflow-hidden relative border shadow-lg
                                    ${isExpanded ? 'bg-[#15100e]/95 border-[#5c4025] shadow-2xl scale-[1.01]' : 'bg-[#0f0a08]/80 border-[#2a1d15] hover:bg-[#1a1512] hover:border-[#3e2b22]'}
                                    ${isWinner && !isExpanded ? 'border-[#8c6239]/50 shadow-[0_0_20px_rgba(212,175,55,0.1)]' : ''}
                                `}
                            >
                                {/* Glass Reflection */}
                                <div className="absolute inset-0 bg-gradient-to-br from-white/5 to-transparent pointer-events-none"></div>
                                {/* Winner Glow Element */}
                                {isWinner && <div className="absolute left-0 top-0 bottom-0 w-1 bg-gradient-to-b from-[#d4af37] to-[#8c6239] opacity-80 shadow-[0_0_15px_#d4af37]"></div>}

                                <div onClick={() => toggleExpand(res.playerId)} className="grid grid-cols-12 gap-2 items-center p-4 cursor-pointer relative z-20">
                                    {/* Player Info */}
                                    <div className="col-span-8 md:col-span-4 flex items-center gap-4">
                                        <div className="relative">
                                            <div className={`w-12 h-12 md:w-14 md:h-14 rounded-full border-2 overflow-hidden shrink-0 shadow-lg ${isBanker ? 'border-[#ef4444] ring-2 ring-[#7f1d1d]/50' : 'border-[#3e2b22]'}`}>
                                                <img src={avatarUrl} alt="" className="w-full h-full object-cover filter brightness-90 grayscale-[0.2]" loading="lazy" />
                                            </div>
                                            {isBanker && (
                                                <div className="absolute -top-1 -right-1 w-5 h-5 bg-[#8c1c0b] text-[#ffd700] flex items-center justify-center rounded-full text-[10px] border border-[#d4af37] shadow-md z-10 font-serif font-bold"></div>
                                            )}
                                            <div className="absolute -bottom-1 -right-1 w-5 h-3 bg-[#1a0505] border border-[#5c4025] shadow-md transform rotate-12 z-10 flex items-center justify-center p-[1px]">
                                                <img src={flagUrl} alt={profile?.country} className="w-full h-full object-cover opacity-90" loading="lazy" />
                                            </div>
                                        </div>
                                        
                                        <div className="flex flex-col">
                                            <div className="flex items-center gap-2">
                                                <span className={`text-base md:text-lg font-bold tracking-wider truncate font-serif ${isMe ? 'text-[#ffd700]' : 'text-[#d9c7b0]'}`}>
                                                    {truncateName(profile?.username || playerObj?.name || '')}
                                                </span>
                                            </div>
                                            <div className="flex items-center gap-2 mt-1">
                                                {!isBanker && (
                                                    <span className={`text-[9px] border px-1.5 py-0.5 rounded-[2px] ${diaoBadge.bg} ${diaoBadge.border} ${diaoBadge.textCol}`}>
                                                        {diaoBadge.text}
                                                    </span>
                                                )}
                                                <span className="md:hidden text-[9px] text-[#5c4025] uppercase tracking-wide">
                                                    {isExpanded ? 'Hide' : 'Details'}
                                                </span>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Desktop Columns */}
                                    <div className={`hidden md:block col-span-1 text-center font-mono text-sm text-[#78716c]`}>{res.diaoPointChange}</div>
                                    <div className={`hidden md:block col-span-1 text-center font-mono text-sm text-[#78716c]`}>{res.kaiZhuPointChange}</div>
                                    <div className={`hidden md:block col-span-1 text-center font-mono text-sm text-[#78716c]`}>{res.qiaoMenPointChange}</div>
                                    <div className={`hidden md:block col-span-1 text-center font-mono text-sm text-[#78716c]`}>{res.seYangPointChange}</div>
                                    <div className={`hidden md:block col-span-1 text-center font-mono text-sm text-[#78716c]`}>{res.kaiChongPointChange}</div>
                                    
                                    {/* Total Score */}
                                    <div className="col-span-4 md:col-span-3 text-right pr-2">
                                        <span className={`text-2xl md:text-4xl ${getScoreGradientClass(res.totalRoundChange)}`}>
                                            {res.totalRoundChange > 0 ? '+' : ''}{res.totalRoundChange}
                                        </span>
                                    </div>
                                </div>
                                {isExpanded && <RenderVisuals res={res} />}
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* Footer Buttons */}
            <div className="relative z-30 bg-gradient-to-t from-[#050202] via-[#0a0503] to-transparent pt-12 pb-8 md:pb-12 px-8 shrink-0 flex justify-center gap-4 md:gap-8">
                 <ModalButton label={t('home')} onClick={onHome} icon="" />
                 <ModalButton label={t('nextRound')} onClick={onNextRound} primary icon="" />
            </div>
        </div>
    );
};

// --- SETTINGS COMPONENTS ---

const RecorderView = ({ recordedCards, faceUpPlayedCardIds }: { recordedCards: Card[], faceUpPlayedCardIds: Set<string> }) => {
    const visibleCards = useMemo(() => recordedCards.filter(c => faceUpPlayedCardIds.has(c.id)), [recordedCards, faceUpPlayedCardIds]);
    const grouped = useMemo(() => {
        const groups: Record<string, Card[]> = { [Suit.CASH]: [], [Suit.STRINGS]: [], [Suit.COINS]: [], [Suit.TEXTS]: [] };
        visibleCards.forEach(c => { if (groups[c.suit]) groups[c.suit].push(c); });
        Object.keys(groups).forEach(key => { groups[key].sort((a, b) => b.value - a.value); });
        return groups;
    }, [visibleCards]);
    const suitsOrder = [Suit.CASH, Suit.STRINGS, Suit.COINS, Suit.TEXTS];
    const suitLabels: Record<string, string> = { [Suit.CASH]: 'Cash / ', [Suit.STRINGS]: 'Strings / ', [Suit.COINS]: 'Coins / ', [Suit.TEXTS]: 'Texts / ' };

    return (
        <div className="flex flex-col h-full overflow-hidden">
            <div className="flex justify-between items-center mb-4 px-2 shrink-0">
                <h4 className="text-[#c5a059] text-sm uppercase tracking-widest font-bold">Public Knowledge</h4>
                <span className="text-xs text-[#5c4025]">Face-Up: {visibleCards.length} / {recordedCards.length}</span>
            </div>
            <div className="flex-1 overflow-y-auto custom-scrollbar space-y-6 pr-2">
                {suitsOrder.map(suit => (
                    <div key={suit} className="bg-[#0a0806] border border-[#2a1d15] rounded-sm p-3 relative">
                        <div className="absolute -top-2 left-3 bg-[#15100e] px-2 text-[10px] text-[#8c6239] uppercase tracking-widest font-bold border border-[#2a1d15]">{suitLabels[suit]}</div>
                        <div className="flex flex-wrap gap-2 mt-2">
                            {grouped[suit].length > 0 ? (grouped[suit].map((card, i) => (<div key={`${card.id}-${i}`} className="transform hover:scale-110 transition-transform origin-bottom duration-200"><CardComponent card={card} isSmall className="shadow-md border-white/5" /></div>))) : (<span className="text-[10px] text-[#3e2b22] italic w-full text-center py-2">No visible cards</span>)}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

const HistoryView = ({ history }: { history: GameHistory }) => (
    <div className="flex flex-col h-full overflow-y-auto custom-scrollbar">
        <div className="grid grid-cols-2 gap-4 mb-6 text-[#a0a0a0] shrink-0">
            <div className="bg-[#0a0806] p-4 border border-[#2a1d15] text-center rounded-sm"><div className="text-[10px] text-[#5c4025] uppercase tracking-widest mb-1">Total Rounds</div><div className="text-2xl text-[#e6c278] font-bold font-serif">{history.totalRounds}</div></div>
            <div className="bg-[#0a0806] p-4 border border-[#2a1d15] text-center rounded-sm"><div className="text-[10px] text-[#5c4025] uppercase tracking-widest mb-1">Banker vs Peasant</div><div className="text-xl text-[#e6c278] font-serif">{history.bankerWins} : {history.peasantWins}</div></div>
        </div>
        <div className="space-y-3">
            <h4 className="text-[#8c6239] text-[10px] uppercase tracking-widest sticky top-0 bg-[#15100e] py-2 border-b border-[#2a1d15] mb-2">Opponent Intelligence</h4>
            {Object.entries(history.aiLearningStates || {}).map(([pid, state]: [string, any]) => (
                <div key={pid} className="bg-[#0a0806] p-3 border border-[#2a1d15] flex justify-between items-center rounded-sm hover:border-[#3e2b22] transition-colors">
                    <span className="text-[#c5a059] text-xs font-bold">Seat {pid}</span>
                    <div className="text-[10px] text-[#5c4025] flex gap-3 font-mono"><span>AGR: <span className="text-[#a0a0a0]">{state.aggression.toFixed(2)}</span></span><span>RSK: <span className="text-[#a0a0a0]">{state.riskTolerance.toFixed(2)}</span></span></div>
                </div>
            ))}
        </div>
    </div>
);

const GeneralSettingsView = ({ settings, onOpenAdmin }: { settings: GameSettings, onOpenAdmin: () => void }) => {
    const { language, setLanguage, oneClickPlay, setOneClickPlay, isRiskAlertOn, setIsRiskAlertOn, isMuted, setIsMuted, graphicsQuality, setGraphicsQuality, showPaperDolls, setShowPaperDolls, isMobileDevice, baseScore, setBaseScore } = settings;
    const { skin, setSkinId, availableSkins } = useSkin(); 
    const Row = ({ label, children }: any) => ( <div className="flex justify-between items-center text-[#a0a0a0] py-3 border-b border-[#2a1d15]/50"> <span className="text-xs uppercase tracking-wide text-[#8c6239] font-bold">{label}</span> {children} </div> );
    const Toggle = ({ active, onClick, labelOn = "ON", labelOff = "OFF" }: any) => ( <button onClick={onClick} className={`px-4 py-1.5 text-[10px] border transition-all duration-300 font-bold tracking-wider rounded-full ${active ? 'border-[#c5a059] text-[#15100e] bg-[#c5a059]' : 'border-[#3e2b22] text-[#5c4025] hover:text-[#8c6239]'}`}> {active ? labelOn : labelOff} </button> );
    
    // SAFE TRANSLATION LOOKUP (Prevents undefined dictionary access)
    const t = (key: string) => {
        // Fallback to 'zh_CN' if language is undefined (shouldn't happen) or 'en' if key missing
        const lang = (language || 'zh_CN') as SupportedLanguage;
        const dict = TRANSLATIONS[lang] || TRANSLATIONS['en'];
        return dict?.[key] || key;
    };

    return (
        <div className="space-y-2 h-full overflow-y-auto custom-scrollbar pr-2 pt-2">
            <div className="space-y-1">
                <div className="text-[10px] text-[#5c4025] uppercase tracking-[0.3em] mb-4 font-bold border-b border-[#3e2b22] pb-2">{t('setting_gameplay')}</div>
                <Row label={t('setting_theme')}> <select value={skin.id} onChange={(e) => setSkinId(e.target.value)} className="bg-[#0a0806] border border-[#3e2b22] text-[#c5a059] px-3 py-1.5 text-[10px] outline-none rounded-full appearance-none text-center min-w-[100px]"> {availableSkins.map((s:any) => <option key={s.id} value={s.id}>{t(`skin_${s.id.replace('_scholar', '').replace('_mystery', '')}`) || s.name}</option>)} </select> </Row>
                <Row label={t('setting_graphics')}> <div className="flex gap-2"> <button onClick={() => setGraphicsQuality('LOW')} className={`px-3 py-1.5 text-[10px] border rounded-full transition-colors ${graphicsQuality === 'LOW' ? 'border-[#c5a059] text-[#c5a059] bg-[#c5a059]/10' : 'border-[#3e2b22] text-[#5c4025]'}`}>Low</button> <button onClick={() => setGraphicsQuality('HIGH')} className={`px-3 py-1.5 text-[10px] border rounded-full transition-colors ${graphicsQuality === 'HIGH' ? 'border-[#c5a059] text-[#c5a059] bg-[#c5a059]/10' : 'border-[#3e2b22] text-[#5c4025]'}`}>High</button> </div> </Row>
                
                {!isMobileDevice && (
                    <Row label={t('setting_avatars')}> <Toggle active={showPaperDolls} onClick={() => setShowPaperDolls(!showPaperDolls)} labelOn="Show" labelOff="Hide" /> </Row>
                )}
                
                <Row label={t('setting_lang')}> <select value={language} onChange={(e:any) => setLanguage(e.target.value)} className="bg-[#0a0806] border border-[#3e2b22] text-[#c5a059] px-3 py-1.5 text-[10px] outline-none rounded-full appearance-none text-center min-w-[100px]"> {['en', 'zh_CN', 'zh_TW', 'ja', 'ko'].map(l => <option key={l} value={l}>{l}</option>)} </select> </Row>
                <Row label={t('setting_control')}> <Toggle active={oneClickPlay} onClick={() => setOneClickPlay(!oneClickPlay)} labelOn={t('oneClickPlay')} labelOff="Confirm" /> </Row>
                <Row label={t('setting_hints')}> <Toggle active={isRiskAlertOn} onClick={() => setIsRiskAlertOn(!isRiskAlertOn)} /> </Row>
                <Row label={t('setting_sound')}> <Toggle active={!isMuted} onClick={() => setIsMuted(!isMuted)} /> </Row>
                <Row label={t('setting_base')}> 
                    <select value={baseScore} onChange={(e:any) => setBaseScore(Number(e.target.value))} className="bg-[#0a0806] border border-[#3e2b22] text-[#c5a059] px-3 py-1.5 text-[10px] outline-none rounded-full appearance-none text-center min-w-[100px]"> 
                        {[1, 2, 5, 10, 20, 50, 100].map(s => <option key={s} value={s}>{s}</option>)} 
                    </select> 
                </Row>
            </div>
        </div>
    );
};

export interface SettingsModalProps { onClose: () => void; onOpenAdmin: () => void; settings: GameSettings; initialTab?: SettingsTab; gameData?: { recordedCards: Card[]; faceUpPlayedCardIds: Set<string>; gameHistory: GameHistory; }; }

export const SettingsModal = ({ onClose, onOpenAdmin, settings, initialTab = 'GENERAL', gameData }: SettingsModalProps) => {
    const { skin } = useSkin();
    const { isMobileDevice } = useGameSettings();
    const [activeTab, setActiveTab] = useState<SettingsTab>(initialTab);
    const IconExportTarget = () => ( <div className="absolute top-0 left-0 -z-50 opacity-0 pointer-events-none" style={{ width: '1024px', height: '1024px' }}> <AppIcon id="app-icon-export-target" size={1024} /> </div> );
    const TabButton = ({ id, label, icon }: { id: SettingsTab, label: string, icon: string }) => ( <button onClick={() => setActiveTab(id)} className={`flex-1 py-4 text-[10px] uppercase tracking-widest font-bold transition-all duration-300 flex flex-col items-center gap-1 border-t-2 ${activeTab === id ? 'text-[#c5a059] border-[#c5a059] bg-[#c5a059]/10' : 'text-[#5c4025] border-transparent hover:text-[#8c6239] hover:bg-white/5'}`}> <span className="text-lg opacity-80">{icon}</span> <span className="scale-90">{label}</span> </button> );

    const modalStyle = `${skin.hud.modalContentClass} !rounded-b-none !rounded-t-[24px] !border-b-0 w-full max-w-full md:max-w-md h-[85vh] absolute bottom-0 flex flex-col shadow-[0_-20px_60px_rgba(0,0,0,0.9)] border-t border-l border-r border-[#3e2b22] overflow-hidden`;

    return (
        <div className="fixed inset-0 z-[300] flex items-end justify-center bg-black/80 backdrop-blur-sm animate-fade-in font-serif">
            <IconExportTarget />
            <div className={modalStyle}>
                {/* Shine Effect */}
                <div className="absolute top-0 left-0 w-full h-[1px] bg-gradient-to-r from-transparent via-[#ffd700] to-transparent opacity-50"></div>
                
                {/* Header with Back Button */}
                <div className="flex items-center justify-between px-6 py-5 border-b border-white/10 bg-black/20 shrink-0 relative">
                    <button 
                        onClick={onClose} 
                        className="text-[#c5a059] hover:text-white transition-transform active:scale-90 p-2 -ml-2"
                    >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                        </svg>
                    </button>
                    <span className="text-sm font-bold tracking-[0.2em] text-[#8c6239] uppercase">
                        {activeTab === 'GENERAL' ? 'Settings' : activeTab}
                    </span>
                    {/* Invisible Admin Trigger Area */}
                    <button onClick={onOpenAdmin} className="w-10 h-10 opacity-0 cursor-default" title="Admin Portal"></button>
                </div>

                <div className="flex-1 p-6 relative z-10 overflow-hidden bg-black/10">
                    {activeTab === 'RECORDER' && gameData && ( <RecorderView recordedCards={gameData.recordedCards} faceUpPlayedCardIds={gameData.faceUpPlayedCardIds} /> )}
                    {activeTab === 'GENERAL' && ( <GeneralSettingsView settings={settings} onOpenAdmin={onOpenAdmin} /> )}
                    {activeTab === 'HISTORY' && gameData && ( <HistoryView history={gameData.gameHistory} /> )}
                    {((activeTab === 'RECORDER' || activeTab === 'HISTORY') && !gameData) && ( <div className="flex h-full items-center justify-center text-[#5c4025] italic text-xs"> Game data not available. </div> )}
                </div>

                {/* Bottom Navigation Tabs */}
                <div className="flex border-t border-white/10 bg-black/40 shrink-0 z-20 relative pb-safe"> 
                    <TabButton id="GENERAL" label="Config" icon="" /> 
                    <TabButton id="RECORDER" label="Recorder" icon="" /> 
                    <TabButton id="HISTORY" label="History" icon="" /> 
                </div>
            </div>
        </div>
    );
};
