
import { useRef, useEffect, useCallback } from 'react';
import { BGM_URL, AUDIO_CONFIG } from '../services/audioAssets';

type SoundType = 'WOOD' | 'STONE' | 'PAPER' | 'CHIME' | 'DRUM';

// Audio System Hook
export const useAudioSystem = (isMuted: boolean) => {
    const audioContextRef = useRef<AudioContext | null>(null);
    const bgmRef = useRef<HTMLAudioElement | null>(null);
    const gainNodeRef = useRef<GainNode | null>(null);

    // Initialize Audio Context
    useEffect(() => {
        const AudioCtx = window.AudioContext || (window as any).webkitAudioContext;
        if (AudioCtx) {
            const ctx = new AudioCtx({ sampleRate: 24000 });
            audioContextRef.current = ctx;
            
            const gain = ctx.createGain();
            gain.gain.value = AUDIO_CONFIG.sfxVolume;
            gain.connect(ctx.destination);
            gainNodeRef.current = gain;
        }

        const audio = new Audio(BGM_URL);
        audio.loop = true;
        audio.volume = 0; 
        bgmRef.current = audio;

        return () => {
            audioContextRef.current?.close();
            if (bgmRef.current) {
                bgmRef.current.pause();
                bgmRef.current = null;
            }
        };
    }, []);

    // Handle Mute/Unmute
    useEffect(() => {
        if (!bgmRef.current) return;
        if (isMuted) {
            const fadeOut = setInterval(() => {
                if (bgmRef.current && bgmRef.current.volume > 0.01) {
                    bgmRef.current.volume -= 0.02;
                } else {
                    bgmRef.current?.pause();
                    clearInterval(fadeOut);
                }
            }, 100);
        } else {
            if (bgmRef.current.paused) {
                const playPromise = bgmRef.current.play();
                if (playPromise !== undefined) {
                    playPromise.then(() => {
                        const fadeIn = setInterval(() => {
                            if (bgmRef.current && bgmRef.current.volume < AUDIO_CONFIG.bgmVolume) {
                                bgmRef.current.volume = Math.min(AUDIO_CONFIG.bgmVolume, bgmRef.current.volume + 0.01);
                            } else {
                                clearInterval(fadeIn);
                            }
                        }, 200);
                    }).catch(error => {
                        console.log("BGM Autoplay prevented:", error);
                    });
                }
            }
        }
    }, [isMuted]);

    // --- ZEN SFX GENERATION ---
    const playProceduralSFX = useCallback((type: SoundType) => {
        if (isMuted || !audioContextRef.current || !gainNodeRef.current) return;
        const ctx = audioContextRef.current;
        
        if (ctx.state === 'suspended') {
            ctx.resume();
        }

        const t = ctx.currentTime;

        switch (type) {
            case 'WOOD': // Temple Block (Keep existing soft wood)
                const oscWood = ctx.createOscillator();
                const gainWood = ctx.createGain();
                oscWood.connect(gainWood);
                gainWood.connect(gainNodeRef.current);
                
                oscWood.type = 'sine';
                oscWood.frequency.setValueAtTime(300, t);
                oscWood.frequency.exponentialRampToValueAtTime(150, t + 0.1);
                
                gainWood.gain.setValueAtTime(0, t);
                gainWood.gain.linearRampToValueAtTime(0.5, t + 0.02);
                gainWood.gain.exponentialRampToValueAtTime(0.01, t + 0.3);
                
                oscWood.start(t);
                oscWood.stop(t + 0.35);
                break;

            case 'STONE': // NEW: "Walking on Dry Leaves" (Soft Crunch)
            case 'PAPER': // Reuse for shuffle
                const bufferSize = ctx.sampleRate * 0.4; // 400ms
                const buffer = ctx.createBuffer(1, bufferSize, ctx.sampleRate);
                const data = buffer.getChannelData(0);
                for (let i = 0; i < bufferSize; i++) {
                    data[i] = (Math.random() * 2 - 1);
                }
                const noise = ctx.createBufferSource();
                noise.buffer = buffer;

                // Bandpass Filter to simulate dry leaves freq range
                const filter = ctx.createBiquadFilter();
                filter.type = 'bandpass';
                filter.frequency.setValueAtTime(1200, t);
                filter.Q.value = 1.0;

                const gainLeaf = ctx.createGain();
                
                noise.connect(filter);
                filter.connect(gainLeaf);
                gainLeaf.connect(gainNodeRef.current);

                // Irregular volume envelope for "crunch" texture
                gainLeaf.gain.setValueAtTime(0, t);
                gainLeaf.gain.linearRampToValueAtTime(0.4, t + 0.05); // Attack
                gainLeaf.gain.exponentialRampToValueAtTime(0.01, t + 0.3); // Decay

                noise.start(t);
                break;

            case 'CHIME': // NEW: "Cave Echo Knock" (Kai Chong)
            case 'DRUM':
                // 1. The Impact (Low Thud)
                const oscImpact = ctx.createOscillator();
                const gainImpact = ctx.createGain();
                oscImpact.type = 'triangle';
                oscImpact.frequency.setValueAtTime(150, t);
                oscImpact.frequency.exponentialRampToValueAtTime(40, t + 0.3);
                
                gainImpact.gain.setValueAtTime(0, t);
                gainImpact.gain.linearRampToValueAtTime(0.8, t + 0.02);
                gainImpact.gain.exponentialRampToValueAtTime(0.01, t + 0.5);

                // 2. The Echo (Delay Loop)
                const delayNode = ctx.createDelay();
                delayNode.delayTime.value = 0.35; // 350ms echo
                
                const feedbackGain = ctx.createGain();
                feedbackGain.gain.value = 0.4; // Decay rate of echo

                const echoFilter = ctx.createBiquadFilter();
                echoFilter.type = 'lowpass';
                echoFilter.frequency.value = 600; // Dampen high freq in echo

                // Routing: Osc -> Gain -> Main
                //       -> Gain -> Delay -> EchoFilter -> Feedback -> Delay
                //       -> Gain -> Delay -> Main
                
                oscImpact.connect(gainImpact);
                gainImpact.connect(gainNodeRef.current); // Direct sound
                
                gainImpact.connect(delayNode);
                delayNode.connect(echoFilter);
                echoFilter.connect(feedbackGain);
                feedbackGain.connect(delayNode); // Loop
                
                delayNode.connect(gainNodeRef.current); // Echo sound

                oscImpact.start(t);
                oscImpact.stop(t + 2.0); // Allow time for echo tail
                break;
        }
    }, [isMuted]);

    // TTS Decoder (Unchanged)
    const decodeAudioData = async (data: Uint8Array, ctx: AudioContext, sampleRate: number, numChannels: number): Promise<AudioBuffer> => {
        const dataInt16 = new Int16Array(data.buffer);
        const frameCount = dataInt16.length / numChannels;
        const buffer = ctx.createBuffer(numChannels, frameCount, sampleRate);
        for (let channel = 0; channel < numChannels; channel++) {
            const channelData = buffer.getChannelData(channel);
            for (let i = 0; i < frameCount; i++) {
                channelData[i] = dataInt16[i * numChannels + channel] / 32768.0;
            }
        }
        return buffer;
    };

    const playPcmAudio = useCallback(async (pcmData: Uint8Array) => {
        if (isMuted || !audioContextRef.current || !gainNodeRef.current) return;
        try {
            if (audioContextRef.current.state === 'suspended') {
                await audioContextRef.current.resume();
            }
            const audioBuffer = await decodeAudioData(pcmData, audioContextRef.current, 24000, 1);
            const source = audioContextRef.current.createBufferSource();
            source.buffer = audioBuffer;
            const voiceGain = audioContextRef.current.createGain();
            voiceGain.gain.value = 0.8; 
            source.connect(voiceGain);
            voiceGain.connect(gainNodeRef.current);
            source.start();
        } catch (e) {
            console.error("Audio playback error", e);
        }
    }, [isMuted]);

    const startBGM = useCallback(() => {
        if (!isMuted && bgmRef.current && bgmRef.current.paused) {
            bgmRef.current.play().catch(e => console.log("Interaction needed for BGM"));
        }
    }, [isMuted]);

    return { playPcmAudio, playProceduralSFX, startBGM };
};
