// Generating the Ma Diao Deck (40 Cards) based on the PDF table
export const generateDeck = (): Card[] => {
  const deck: Card[] = [];

  // 1. CASH SUIT () - 11 Cards
  deck.push(createCard(Suit.CASH, '', 11, CardColor.RED, CardRank.ZUN, '', ID_WAN_WAN)); 
  deck.push(createCard(Suit.CASH, '', 10, CardColor.RED, CardRank.JIAN, '', ID_QIAN_WAN));
  deck.push(createCard(Suit.CASH, '', 9, CardColor.RED, CardRank.BAI, '', ID_BAI_WAN));
  deck.push(createCard(Suit.CASH, '', 8, CardColor.BLACK, CardRank.QING, '', ID_90_WAN)); 
  deck.push(createCard(Suit.CASH, '', 7, CardColor.BLACK, CardRank.QING, '', 'c_7'));
  deck.push(createCard(Suit.CASH, '', 6, CardColor.BLACK, CardRank.QING, '', 'c_6'));
  deck.push(createCard(Suit.CASH, '', 5, CardColor.BLACK, CardRank.QING, '', 'c_5'));
  deck.push(createCard(Suit.CASH, '', 4, CardColor.BLACK, CardRank.QING, '', 'c_4'));
  deck.push(createCard(Suit.CASH, '', 3, CardColor.BLACK, CardRank.QING, '', 'c_3'));
  deck.push(createCard(Suit.CASH, '', 2, CardColor.BLACK, CardRank.QING, '', 'c_2'));
  deck.push(createCard(Suit.CASH, '', 1, CardColor.GREEN, CardRank.JI, '', ID_20_WAN));

  // 2. STRINGS SUIT () - 9 Cards
  deck.push(createCard(Suit.STRINGS, '', 9, CardColor.RED, CardRank.ZUN, '', ID_9_GUAN));
  deck.push(createCard(Suit.STRINGS, '', 8, CardColor.RED, CardRank.JIAN, '', ID_8_GUAN));
  deck.push(createCard(Suit.STRINGS, '', 7, CardColor.BLACK, CardRank.QING, '', 's_7'));
  deck.push(createCard(Suit.STRINGS, '', 6, CardColor.BLACK, CardRank.QING, '', ID_6_GUAN));
  deck.push(createCard(Suit.STRINGS, '', 5, CardColor.BLACK, CardRank.QING, '', ID_5_GUAN));
  deck.push(createCard(Suit.STRINGS, '', 4, CardColor.BLACK, CardRank.QING, '', 's_4'));
  deck.push(createCard(Suit.STRINGS, '', 3, CardColor.BLACK, CardRank.QING, '', 's_3'));
  deck.push(createCard(Suit.STRINGS, '', 2, CardColor.BLACK, CardRank.QING, '', 's_2'));
  deck.push(createCard(Suit.STRINGS, '', 1, CardColor.GREEN, CardRank.JI, '', ID_1_GUAN));

  // 3. COINS SUIT () - 9 Cards
  deck.push(createCard(Suit.COINS, '', 9, CardColor.RED, CardRank.ZUN, '', ID_9_SUO));
  deck.push(createCard(Suit.COINS, '', 8, CardColor.RED, CardRank.JIAN, '', 'k_8'));
  deck.push(createCard(Suit.COINS, '', 7, CardColor.BLACK, CardRank.QING, '', 'k_7'));
  deck.push(createCard(Suit.COINS, '', 6, CardColor.BLACK, CardRank.QING, '', 'k_6'));
  deck.push(createCard(Suit.COINS, '', 5, CardColor.BLACK, CardRank.QING, '', 'k_5'));
  deck.push(createCard(Suit.COINS, '', 4, CardColor.BLACK, CardRank.QING, '', 'k_4'));
  deck.push(createCard(Suit.COINS, '', 3, CardColor.BLACK, CardRank.QING, '', 'k_3'));
  deck.push(createCard(Suit.COINS, '', 2, CardColor.BLACK, CardRank.QING, '', 'k_2'));
  deck.push(createCard(Suit.COINS, '', 1, CardColor.GREEN, CardRank.JI, '', ID_1_SUO));

  // 4. TEXTS SUIT () - 11 Cards (Reversed Order)
  // Red: Kong Wen (Zun) - 11
  deck.push(createCard(Suit.TEXTS, '', 11, CardColor.RED, CardRank.ZUN, '', ID_KONG_WEN));
  // Red: Ban Wen (Jian) - 10
  deck.push(createCard(Suit.TEXTS, '', 10, CardColor.RED, CardRank.JIAN, '', ID_BAN_WEN));
  // Black: 1 Wen (High) to 8 Wen (Low)
  deck.push(createCard(Suit.TEXTS, '', 9, CardColor.BLACK, CardRank.QING, '', ID_1_WEN));
  deck.push(createCard(Suit.TEXTS, '', 8, CardColor.BLACK, CardRank.QING, '', 't_8'));
  deck.push(createCard(Suit.TEXTS, '', 7, CardColor.BLACK, CardRank.QING, '', 't_7'));
  deck.push(createCard(Suit.TEXTS, '', 6, CardColor.BLACK, CardRank.QING, '', 't_6'));
  deck.push(createCard(Suit.TEXTS, '', 5, CardColor.BLACK, CardRank.QING, '', 't_5'));
  deck.push(createCard(Suit.TEXTS, '', 4, CardColor.BLACK, CardRank.QING, '', 't_4'));
  deck.push(createCard(Suit.TEXTS, '', 3, CardColor.BLACK, CardRank.QING, '', 't_3'));
  deck.push(createCard(Suit.TEXTS, '', 2, CardColor.BLACK, CardRank.QING, '', 't_2'));
  // Green: 9 Wen (Low) - 1 (Ji)
  deck.push(createCard(Suit.TEXTS, '', 1, CardColor.GREEN, CardRank.JI, '', ID_9_WEN));

  return deck;
};

