
import React, { useMemo, useState, useEffect } from 'react';
import { useMaDiaoGame } from './hooks/useMaDiaoLogic';
import { useAIAgent } from './hooks/useAIAgent';
import { useAudioSystem } from './hooks/useAudioSystem';
import { useGameSettings, GameSettingsProvider } from './hooks/useGameSettings';
import { useUIState } from './hooks/useUIState';
import { useGameInteraction } from './hooks/useGameInteraction';
import { Scene3D } from './components/Visuals/Scene3D';
import { PlayerListHUD, HumanHandHUD } from './components/UI/GameInterface';
import { SettingsModal, ScoreModal } from './components/UI/Modals'; 
import { SocialLoginModal } from './components/UI/SocialLoginModal'; 
import { GamePhase, PlayerType, RiskLevel, UserProfile } from './types';
import Chat from './components/Chat';
import { getSuitStatusContext } from './services/riskEngine';
import { AdminGate } from './components/Admin/AdminGate';
import { platformService } from './services/platformService'; 
import { SkinProvider, useSkin } from './contexts/SkinContext';
import { AppIcon } from './components/Visuals/AppIcon'; 
import { TRANSLATIONS } from './constants';

// Internal Logger Wrapper
const logApp = (msg: string) => {
    if ((window as any).logToScreen) (window as any).logToScreen(`[App] ${msg}`);
};

// Unified High-Quality Button Component
// UPDATED: Transparency increased (opacity reduced to /70, /60) to allow poem text to show through blur
const StylizedButton = ({ onClick, children, className, disabled }: any) => (
    <button 
        onClick={onClick}
        disabled={disabled}
        className={`
            relative overflow-hidden group
            w-72 md:w-96 h-20 md:h-24
            bg-gradient-to-b from-[#e6c278]/80 via-[#d4af37]/60 to-[#b48a26]/80
            backdrop-blur-md
            border-2 border-[#fcd34d]/60 rounded-full
            text-[#1a0505] font-serif font-black tracking-[0.3em] text-xl md:text-2xl
            shadow-[0_0_30px_rgba(212,175,55,0.2),inset_0_2px_5px_rgba(255,255,255,0.3)]
            transition-all duration-500 ease-out
            hover:from-[#d68535] hover:via-[#b35c44] hover:to-[#8c3b24] hover:text-[#fffaf0] hover:border-[#ffaa00]
            hover:scale-105 hover:shadow-[0_0_60px_rgba(214,133,53,0.5),inset_0_2px_10px_rgba(255,255,255,0.4)]
            active:scale-95 active:shadow-none
            disabled:opacity-50 disabled:cursor-not-allowed disabled:grayscale
            whitespace-nowrap flex items-center justify-center
            ${className}
        `}
    >
        <span className="relative z-10 drop-shadow-sm transition-colors">{children}</span>
        {/* Shine Sweep */}
        <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/40 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-700 ease-in-out pointer-events-none"></div>
        {/* Highlight Top */}
        <div className="absolute top-0 left-0 w-full h-1/2 bg-gradient-to-b from-white/20 to-transparent pointer-events-none rounded-t-full"></div>
    </button>
);

// Ma Xianglan Seal Component
// UPDATED: Layout fixed to traditional Vertical Right-to-Left.
// Grid Layout (2 cols): 
// [ Left-Top (Lan) ] [ Right-Top (Ma) ]
// [ Left-Btm (Yin) ] [ Right-Btm (Xiang) ]
const MaXianglanSeal = () => (
    <div className="w-16 h-16 md:w-20 md:h-20 border-4 border-[#8c1c0b] bg-[#8c1c0b] rounded-sm shadow-[0_10px_20px_rgba(0,0,0,0.8)] grid grid-cols-2 grid-rows-2 p-1 gap-0 box-border relative opacity-90 hover:opacity-100 transition-opacity">
        {/* Grungy Texture Overlay */}
        <div className="absolute inset-0 opacity-40 bg-[url('https://www.transparenttextures.com/patterns/black-felt.png')] mix-blend-multiply pointer-events-none z-10"></div>
        
        {/* Top Left:  (Lan) */}
        <div className="flex items-center justify-center border-b border-r border-[#e8e4d9]/40 relative z-0">
            <span className="text-[#e8e4d9] font-black text-xl leading-none transform scale-x-90 font-serif"></span>
        </div>
        
        {/* Top Right:  (Ma) */}
        <div className="flex items-center justify-center border-b border-[#e8e4d9]/40 relative z-0">
            <span className="text-[#e8e4d9] font-black text-xl leading-none transform scale-x-90 font-serif"></span>
        </div>
        
        {/* Bottom Left:  (Yin) */}
        <div className="flex items-center justify-center border-r border-[#e8e4d9]/40 relative z-0">
            <span className="text-[#e8e4d9] font-black text-xl leading-none transform scale-x-90 font-serif"></span>
        </div>
        
        {/* Bottom Right:  (Xiang) */}
        <div className="flex items-center justify-center relative z-0">
            <span className="text-[#e8e4d9] font-black text-xl leading-none transform scale-x-90 font-serif"></span>
        </div>
    </div>
);

const GameContent: React.FC = () => {
  logApp("Rendering GameContent...");
  
  const { skin, setSkinId, availableSkins } = useSkin(); 
  const [view, setView] = useState<'GAME' | 'ADMIN'>('GAME');
  const [userProfile, setUserProfile] = useState<UserProfile | undefined>(undefined);

  // 1. Settings & UI State
  const settings = useGameSettings();
  const ui = useUIState();

  // Helper for translations
  const t = (key: string) => TRANSLATIONS[settings.language][key] || TRANSLATIONS['en'][key];

  // 3. Systems
  const { playPcmAudio, playProceduralSFX, startBGM } = useAudioSystem(settings.isMuted);

  // 2. Core Game Logic
  const { state, actions } = useMaDiaoGame({
      difficulty: settings.difficulty,
      isRiskAlertOn: settings.isRiskAlertOn,
      language: settings.language,
      playSound: playProceduralSFX,
      baseScore: settings.baseScore
  });
  
  // 4. AI Agent
  const { aiChatMessages } = useAIAgent({
      gameState: state,
      gameActions: actions,
      audioSystem: { playPcmAudio },
      isMuted: settings.isMuted,
      language: settings.language
  });

  // 5. Derived State & Interaction Logic
  const humanPlayer = state.players.find(p => p.type === PlayerType.HUMAN);
  
  const { interactionState, handle3DActionClick } = useGameInteraction(
      state, 
      actions, 
      humanPlayer, 
      settings
  );

  // 6. Platform Init
  useEffect(() => {
      const initPlatform = async () => {
          logApp("Initializing Platform Service...");
          await platformService.init();
          const session = await platformService.auth.checkSession();
          if (session.success && session.user) {
              setUserProfile(session.user);
          }
      };
      initPlatform();
  }, []);

  // 7. Visual Markers Logic
  const humanCardMarkers = useMemo(() => { 
      if (!humanPlayer) return {}; 
      const markers: any = {};
      const { bankerId, openedSuits, recordedCards, firstLeadInfo, faceUpPlayedCardIds, bankerFirstLeadCard } = state;
      
      humanPlayer.hand.forEach(card => {
          const status = getSuitStatusContext(humanPlayer, card, bankerId, openedSuits, recordedCards, firstLeadInfo, faceUpPlayedCardIds, bankerFirstLeadCard);
          markers[card.id] = { isMature: status === 'SAFE', isForbidden: status === 'FORBIDDEN' };
      });
      return markers;
  }, [humanPlayer, state.openedSuits, state.recordedCards, state.bankerId, state.firstLeadInfo, state.faceUpPlayedCardIds, state.bankerFirstLeadCard]);

  // --- Handlers ---
  const handleStartGame = () => {
      startBGM(); // Start music on user interaction
      actions.startGame(userProfile);
  };
  
  const handleQuitGame = () => {
      if (confirm(t('quitConfirm'))) {
          actions.exitToTitle();
      }
  };

  const openRecorder = () => {
      ui.setActiveSettingsTab('RECORDER');
      ui.setShowSettings(true);
  };

  const openSettings = () => {
      ui.setActiveSettingsTab('GENERAL');
      ui.setShowSettings(true);
  };

  const getSkinColor = (id: string) => {
      switch(id) {
          case 'imperial': return 'from-[#8c1c0b] to-[#3d0e0e] border-[#d4af37]';
          case 'christmas': return 'from-[#0f2e1c] to-[#051a0d] border-[#b91c1c]';
          case 'ming_scholar': return 'from-[#5c3a21] to-[#3e2b22] border-[#8c6239]';
          case 'taoist_mystery': return 'from-[#37474f] to-[#263238] border-[#90a4ae]';
          default: return 'from-gray-500 to-gray-700 border-white';
      }
  };

  if (view === 'ADMIN') return <AdminGate onExit={() => setView('GAME')} />;

  const { phase, currentPlayerIndex, bankerId, selectedCardId, trickNumber, mianZhangCard } = state;

  return (
    <div className={`fixed inset-0 perspective-container overflow-hidden ${skin.layout.backgroundClass} transition-colors duration-1000`}> 
      <Scene3D 
          state={state} 
          interactionState={{
              ...interactionState,
              onActionClick: handle3DActionClick
          }}
      />

      {phase !== GamePhase.DEALING && phase !== GamePhase.SHUFFLING && (
          <>
            <PlayerListHUD 
                players={state.players} 
                bankerId={bankerId} 
                currentPlayerIndex={currentPlayerIndex} 
                aiChatMessages={aiChatMessages}
            />
            {humanPlayer && (
                <HumanHandHUD 
                    player={humanPlayer} 
                    isMyTurn={state.players[currentPlayerIndex]?.id === 0 && phase === GamePhase.PLAYING} 
                    onCardClick={(id: string) => { 
                        if (settings.oneClickPlay) actions.executePlayCard(0, id);
                        else actions.setSelectedCardId(id === selectedCardId ? null : id);
                    }}
                    onDragPlay={(id: string) => actions.executePlayCard(0, id)} 
                    onPlayConfirm={(id: string) => actions.executePlayCard(0, id)} 
                    selectedCardId={selectedCardId} 
                    highlightedCardIds={interactionState.highlightedCardIds}
                    cardMarkers={humanCardMarkers}
                    oneClickPlay={settings.oneClickPlay}
                    canInteract={!interactionState.disabled}
                    actionLabel={interactionState.label}
                    mianZhangCard={mianZhangCard}
                />
            )}
            <div className="fixed top-4 right-4 z-[200] flex gap-3">
                <button 
                    onClick={openRecorder} 
                    className="text-[#e6c278] bg-black/40 p-2 rounded-full border border-[#5c4025] hover:bg-[#1a0f0a] hover:border-[#c5a059] backdrop-blur-md transition-all shadow-gem-btn w-12 h-12 flex items-center justify-center group" 
                    title="Recorder"
                >
                    <span className="text-xl group-hover:scale-110 transition-transform"></span>
                </button>
                <button 
                    onClick={openSettings} 
                    className="text-[#8c6239] bg-black/40 p-2 rounded-full border border-[#3e2b22] hover:bg-[#1a0f0a] hover:border-[#c5a059] hover:text-[#c5a059] backdrop-blur-md transition-all shadow-gem-btn w-12 h-12 flex items-center justify-center group" 
                    title="Menu"
                >
                    <span className="text-xl group-hover:rotate-90 transition-transform duration-500"></span>
                </button>
            </div>
          </>
      )}

      {/* Warning Modal */}
      {state.pendingRisk && (
          <div className="fixed inset-0 z-[400] flex items-center justify-center bg-black/30 backdrop-blur-sm p-4">
              <div className="pointer-events-auto bg-black/60 backdrop-blur-xl border border-white/10 p-8 w-full max-w-sm rounded-lg shadow-[0_20px_50px_rgba(0,0,0,0.8)] flex flex-col items-center text-center relative animate-fade-in ring-1 ring-white/5">
                  <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-transparent via-[#ef4444] to-transparent opacity-80"></div>
                  
                  <div className="flex items-center gap-3 mb-6">
                      <div className="text-[#ef4444] text-3xl animate-pulse drop-shadow-[0_0_10px_rgba(239,68,68,0.6)]">!</div>
                      <h3 className="text-xl text-[#fca5a5] font-serif font-bold tracking-widest drop-shadow-md">
                          {state.pendingRisk.assessment.riskLevel === RiskLevel.PENALTY ? t('risk_penalty') : t('risk_warning')}
                      </h3>
                  </div>
                  
                  <div className="text-[#d4af37] text-xs uppercase tracking-[0.2em] mb-6 border-b border-white/10 pb-2 w-full opacity-80">
                      {state.pendingRisk.assessment.ruleId}
                  </div>
                  
                  <p className="text-gray-200 mb-8 leading-relaxed font-serif text-base px-2 drop-shadow-sm">
                      {state.pendingRisk.assessment.message}
                  </p>
                  
                  <div className="flex flex-col w-full gap-4">
                      <button 
                          onClick={() => actions.executePlayCard(0, state.pendingRisk!.cardId, true)} 
                          className="w-full py-4 bg-gradient-to-br from-[#d4af37] via-[#b48a26] to-[#8c6239] text-[#1a0505] shadow-[0_0_20px_rgba(212,175,55,0.3)] border border-[#fcd34d]/50 font-serif font-bold tracking-[0.2em] text-sm hover:brightness-110 active:scale-95 transition-all rounded-[4px]"
                      >
                          {t('risk_confirm_penalty')}
                      </button>
                      <button 
                          onClick={() => actions.setPendingRisk(null)} 
                          className="w-full py-4 bg-white/5 border border-white/20 text-gray-300 hover:text-white hover:bg-white/10 backdrop-blur-md font-serif font-bold tracking-[0.2em] text-sm active:scale-95 transition-all rounded-[4px]"
                      >
                          {t('cancel')}
                      </button>
                  </div>
              </div>
          </div>
      )}

      {/* --- REDESIGNED HOME SCREEN V7 (VERTICAL FIX + SMALLER FONT) --- */}
      {phase === GamePhase.DEALING && (
          <div className={`fixed inset-0 z-[300] flex text-[#c5a059] font-serif overflow-hidden transition-all duration-1000 ${skin.layout.backgroundClass}`}>
                {/* 1. Global Atmosphere Base */}
                <div className="absolute inset-0 bg-gradient-to-b from-[#1a0505] via-transparent to-black opacity-80 pointer-events-none z-0"></div>
                
                {/* 2. Heavy Vignette (Focus Center) */}
                <div className="absolute inset-0 bg-[radial-gradient(circle_at_center,transparent_20%,rgba(0,0,0,0.8)_80%,#000_100%)] pointer-events-none z-[1]"></div>

                {/* 3. LAYER 0: POEM BACKGROUND (Background Wall) */}
                {/* Adjusted: Font reduced to 4vh per request. */}
                <div className="absolute inset-0 z-0 flex flex-row-reverse justify-between items-start pl-16 md:pl-32 pr-8 py-10 pointer-events-none opacity-40 mix-blend-screen overflow-visible">
                    {[
                        "", "",
                        "", "",
                        "", "",
                        "", "",
                        "", "",
                        "", ""
                    ].reduce((acc: string[], curr, i, arr) => {
                        // Strict Pair Grouping: 2 lines per column
                        if (i % 2 === 0) acc.push(`${curr}${arr[i+1]}`); // Use wide space for vertical separation
                        return acc;
                    }, []).map((pairString, i) => (
                        <div 
                            key={i} 
                            className="opacity-0 animate-fog-reveal h-full text-[#e6c278] font-calligraphy leading-loose select-none border-l border-[#5c4025]/5 pl-2 whitespace-nowrap" 
                            style={{ 
                                animationDelay: `${i * 0.15}s`, 
                                animationFillMode: 'forwards', 
                                textShadow: '0 0 5px rgba(0,0,0,0.5)',
                                writingMode: 'vertical-rl', // Strict vertical mode
                                textOrientation: 'upright',
                                fontSize: '4vh' // Smaller Font: 4vh
                            }}
                        >
                            {pairString}
                        </div>
                    ))}
                </div>

                {/* 4. LAYER 1: LOGO & SEAL (Strictly Vertical Column at Top-Left) */}
                <div className="absolute top-6 left-6 md:top-8 md:left-8 z-10 flex flex-col items-center gap-6">
                    <div className="flex flex-col items-center gap-4 group">
                        {/* Glow Behind Logo */}
                        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-48 h-96 bg-gradient-to-br from-orange-500/20 via-red-500/10 to-transparent blur-3xl opacity-60 animate-pulse-slow pointer-events-none"></div>
                        
                        {/* Vertical Logo Text: Ma (Top), Diao (Bottom) */}
                        <div className="flex flex-col items-center gap-2 font-calligraphy text-7xl md:text-8xl font-black text-transparent bg-clip-text bg-gradient-to-b from-[#ffd700] via-[#ffaa00] to-[#ff4500] select-none"
                             style={{ 
                                filter: 'drop-shadow(0 0 15px rgba(255,140,0,0.6))',
                                textShadow: '4px 4px 0px rgba(0,0,0,0.5)'
                             }}>
                            <span></span>
                            <span></span>
                        </div>
                        
                        {/* Seal Below Logo */}
                        <div className="transform hover:scale-105 transition-transform duration-500 mt-4">
                            <MaXianglanSeal />
                        </div>
                    </div>
                </div>

                {/* 5. LAYER 2: INTERACTION GROUP (Centered in Lower 2/3rds) */}
                <div className="absolute top-1/3 bottom-0 left-0 right-0 z-20 flex flex-col items-center justify-center gap-12 pointer-events-none">
                    
                    {/* Buttons Container (Interactive) */}
                    <div className="flex flex-col gap-6 items-center pointer-events-auto">
                        <StylizedButton onClick={handleStartGame}>
                            {userProfile ? t('continueGame') : t('enterGame')}
                        </StylizedButton>
                        
                        {!userProfile && (
                            <StylizedButton onClick={() => ui.setShowSocialModal(true)} className="opacity-90 grayscale-[0.2] hover:grayscale-0">
                                {t('netBattle')}
                            </StylizedButton>
                        )}
                        
                        {userProfile && (
                            <button 
                                onClick={async () => { await platformService.auth.logout(); setUserProfile(undefined); actions.exitToTitle(); }} 
                                className="text-sm text-[#8c6239] hover:text-[#d4af37] underline decoration-[#3e2b22] tracking-[0.2em] uppercase transition-colors mt-2"
                            >
                                {t('signOut')}
                            </button>
                        )}
                    </div>

                    {/* Skin Selector Container (Interactive) - Part of the Group */}
                    <div className="w-full px-8 flex justify-center pointer-events-auto">
                        <div className="flex gap-4 md:gap-8 p-4 bg-black/40 backdrop-blur-md rounded-full border border-[#3e2b22] shadow-[0_10px_40px_rgba(0,0,0,0.8)] hover:bg-black/60 transition-colors">
                            {availableSkins.map((s: any) => {
                                const isActive = skin.id === s.id;
                                const colorClass = getSkinColor(s.id);
                                return (
                                    <button
                                        key={s.id}
                                        onClick={() => setSkinId(s.id)}
                                        className={`
                                            w-14 h-14 md:w-16 md:h-16 rounded-full border-2 transition-all duration-500 shadow-xl relative group overflow-hidden
                                            bg-gradient-to-br ${colorClass}
                                            ${isActive ? 'scale-110 shadow-[0_0_25px_rgba(212,175,55,0.6)] ring-2 ring-[#ffd700] brightness-110 -translate-y-2' : 'opacity-70 hover:opacity-100 hover:-translate-y-1 grayscale-[0.5] hover:grayscale-0'}
                                        `}
                                        title={t(`skin_${s.id.replace('_scholar', '').replace('_mystery', '')}`) || s.name}
                                    >
                                        {isActive && (
                                            <div className="absolute inset-0 bg-gradient-to-t from-[#ffd700]/30 to-transparent pointer-events-none"></div>
                                        )}
                                        {/* Glass Shine */}
                                        <div className="absolute top-0 right-0 w-2/3 h-2/3 bg-gradient-to-bl from-white/20 to-transparent rounded-full blur-[1px]"></div>
                                    </button>
                                );
                            })}
                        </div>
                    </div>
                </div>
          </div>
      )}

      {phase === GamePhase.SCORING && (
          <ScoreModal 
              results={state.roundResults} 
              onClose={() => {}} 
              onNextRound={actions.startNextRound}
              onHome={actions.exitToTitle}
              onQuit={handleQuitGame}
              bankerId={bankerId} 
              language={settings.language} 
              players={state.players} 
          />
      )}
      
      {ui.showSettings && (
          <SettingsModal 
            onClose={() => ui.setShowSettings(false)} 
            onOpenAdmin={() => { ui.setShowSettings(false); setView('ADMIN'); }}
            settings={settings} 
            initialTab={ui.activeSettingsTab}
            gameData={{
                recordedCards: state.recordedCards,
                faceUpPlayedCardIds: state.faceUpPlayedCardIds,
                gameHistory: state.gameHistory
            }}
          />
      )}
      
      {ui.showSocialModal && (
          <SocialLoginModal 
            onClose={() => ui.setShowSocialModal(false)}
            onLoginSuccess={(profile: any) => {
                ui.setShowSocialModal(false);
                setUserProfile(profile);
                actions.startGame(profile);
            }}
          />
      )}

      <Chat language={settings.language} />
    </div>
  );
};

export const App: React.FC = () => {
    logApp("App Root Mounted.");
    return (
        <SkinProvider>
            <GameSettingsProvider>
                <GameContent />
            </GameSettingsProvider>
        </SkinProvider>
    );
};
