
import React from 'react';
import { ISkin, CardStyleProps } from '../types/skin';
import { LeadingEffectType, Suit, CardColor } from '../types';
import { TableBorderFlow, StoveLighting } from '../components/Visuals/TableEffects';

// Suspended Gold Dust Pattern (CSS Gradient only)
const GOLD_DUST_PATTERN = "radial-gradient(circle, rgba(255,215,0,0.4) 1px, transparent 1px)"; 

// LOW QUALITY PATTERN: Simplified gradient for mobile
const LOW_QUALITY_BG = 'radial-gradient(circle at 50% 70%, #2b0a0a 0%, #050202 100%)';

const ImperialAtmosphere: React.FC<{ quality?: 'HIGH' | 'LOW' }> = ({ quality }) => (
    <div className="absolute inset-0 pointer-events-none z-0 overflow-hidden bg-[#050202]">
         {quality === 'LOW' ? (
             // --- LOW QUALITY: Static but rich background ---
             <div className="absolute inset-0" style={{ background: LOW_QUALITY_BG }}>
                 <div className="absolute inset-0 bg-[radial-gradient(circle_at_center,transparent_40%,rgba(0,0,0,0.8)_100%)]"></div>
             </div>
         ) : (
             // --- HIGH QUALITY: Original Deep Red Lacquer + Particles ---
             <>
                 {/* 1. Base Layer: Deep Red Lacquer Gradient (Warm dark base) */}
                 <div className="absolute inset-0" style={{ 
                     background: 'radial-gradient(circle at 50% 120%, #5c1515 0%, #2b0a0a 40%, #0f0202 100%)'
                 }}></div>
                 
                 {/* 2. Main Candlelight Glow (Top Left Source) - Warm Amber - Diffuse Reflection */}
                 <div className="absolute top-[-20%] left-[10%] w-[80%] h-[70%] bg-[radial-gradient(circle,rgba(255,160,50,0.25)_0%,transparent_60%)] blur-[100px] animate-candle-flicker mix-blend-screen pointer-events-none"></div>
                 
                 {/* 3. Secondary Ambient Glow (Bottom Right reflection) - Deep Orange */}
                 <div className="absolute bottom-[10%] right-[-10%] w-[60%] h-[60%] bg-[radial-gradient(circle,rgba(200,80,20,0.15)_0%,transparent_60%)] blur-[80px] animate-candle-flicker mix-blend-screen pointer-events-none" style={{ animationDelay: '2.5s', animationDuration: '7s' }}></div>

                 {/* 4. Suspended Gold Dust Particles (Simulating sparks/dust in light) */}
                 <div className="absolute inset-0 opacity-30 mix-blend-overlay pointer-events-none" style={{ overflow: 'hidden' }}>
                     {[...Array(3)].map((_, i) => (
                         <div key={i} 
                              className="absolute w-[200%] h-[200%] animate-dust-float"
                              style={{
                                  backgroundImage: GOLD_DUST_PATTERN,
                                  backgroundSize: `${100 + i * 50}px ${100 + i * 50}px`,
                                  left: `-${i * 10}%`,
                                  top: `-${i * 10}%`,
                                  animationDuration: `${20 + i * 5}s`,
                                  animationDelay: `-${i * 3}s`
                              }}
                         ></div>
                     ))}
                 </div>
             </>
         )}
         
         {/* 5. Vignette to focus on table (Common) - Softened for comfort */}
         <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_60%,transparent_30%,rgba(15,2,2,0.6)_100%)] pointer-events-none"></div>
    </div>
);

const ImperialCardBack: React.FC<{ isSmall?: boolean }> = ({ isSmall }) => (
    <div className="absolute inset-0 w-full h-full rounded-[inherit] overflow-hidden bg-[#0a0502] shadow-inner border border-[#3e1010]">
         <div className="absolute inset-0 opacity-40" style={{ backgroundImage: GOLD_DUST_PATTERN, backgroundSize: '20px 20px' }}></div>
         <div className="absolute inset-0 bg-gradient-to-br from-[#5c1515]/30 to-black/90"></div>
         <div className="absolute inset-1 border border-[#b8860b] rounded-[4px] opacity-50"></div>
         {!isSmall && (
             <div className="absolute inset-0 flex items-center justify-center">
                 <div className="w-8 h-12 border border-[#b8860b] flex items-center justify-center bg-black/60 backdrop-blur-sm shadow-[0_0_15px_#b8860b] animate-pulse-slow rounded-sm">
                    <span className="text-[#b8860b] font-serif font-bold text-lg drop-shadow-[0_0_5px_#b8860b] writing-vertical-rl opacity-90"></span>
                 </div>
             </div>
         )}
    </div>
);

const ImperialEffectOverlay: React.FC<{ effect: LeadingEffectType }> = ({ effect }) => {
    if (!effect) return null;
    return (
        <div className="absolute inset-0 z-30 rounded-[inherit] pointer-events-none overflow-hidden mix-blend-plus-lighter">
            <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(255,215,0,0.3)_0%,transparent_70%)] animate-pulse-slow"></div>
            <div className="absolute inset-[-1px] border-[1.5px] border-[#ffd700] opacity-90 rounded-[inherit] shadow-[0_0_15px_#ffd700]"></div>
        </div>
    );
};

// --- DYNAMIC SHADOWS (THEATER/KABUKI) ---
const TheaterShadows: React.FC = () => (
    <div className="absolute inset-0 pointer-events-none opacity-30 mix-blend-soft-light overflow-hidden" style={{ zIndex: 1 }}>
        {/* Simplified shadows */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/40 to-transparent"></div>
    </div>
);

const BodyFront: React.FC = () => <div className="w-full h-full bg-[#0a0505] opacity-60 blur-xl rounded-full"></div>; 
const ChairStub: React.FC = () => <div className="w-full h-full border-b-4 border-[#3e1010] opacity-50"></div>;

export const ImperialSkin: ISkin = {
    id: 'imperial',
    name: 'Imperial Court (Lacquer)',
    description: 'Deep Urushi lacquer bathed in warm candlelight.',

    layout: {
        backgroundClass: "bg-[#2b0a0a]", // Warm dark red base
        atmosphereComponent: ImperialAtmosphere,
        EnvironmentalShadows: TheaterShadows, 
        
        tableSurfaceClass: "rounded-[32px] shadow-[0_30px_80px_rgba(0,0,0,0.8)] border border-[#2b0a0a]/30 overflow-hidden",
        
        // 1. SOLID BASE (Pitch Black / Deepest Brown)
        // Darkened significantly to create contrast with lighting
        tableBaseColor: '#0a0402',
        
        // 2. TEXTURE (Gold Dust & Lacquer Depth)
        // Upgraded for "Baojiang" (Patina): More complex gradients to simulate oil/age
        // Key change: Added heavy linear gradients to darken the non-center areas
        tableTexture: `
            radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.05) 0%, transparent 40%),
            linear-gradient(160deg, rgba(0,0,0,0.95) 0%, rgba(0,0,0,0.8) 100%),
            radial-gradient(ellipse at 40% 70%, rgba(184, 134, 11, 0.05) 0%, transparent 50%)
        `,
        tableTextureSize: 'auto',
        tableReflectivity: true, 

        tableBorderClass: "bg-[#080202] border-t border-[#3e1010]/20"
    },

    card: {
        getContainerClass: (props: CardStyleProps) => {
            let sizeClass = 'w-16 h-24 md:w-20 md:h-32';
            if (props.isSmall) sizeClass = props.isRotated ? 'w-12 h-8' : 'w-8 h-12';
            else if (props.isTrick) sizeClass = props.isRotated ? 'w-20 h-14 md:w-28 md:h-20' : 'w-14 h-20 md:w-20 md:h-28';
            else if (props.isHand) sizeClass = props.isRotated ? 'w-24 h-16 md:w-28 md:h-20' : 'w-16 h-24 md:w-24 md:h-36'; 
            
            // Refined Colors
            let bgClass = props.isInverted ? 'bg-[#080404] text-[#e6c278] border border-[#3e1010]' : 'bg-[#f7f2e6] text-[#1c1917]'; 
            if (props.isFaceDown) bgClass = 'bg-[#0a0502]';
            if (props.isDisabled) bgClass = 'bg-[#1a1a1a] grayscale opacity-40';
            
            let hoverClass = 'transition-all duration-300 cubic-bezier(0.2, 0.8, 0.2, 1)'; 
            if (!props.isFaceDown && !props.isHand && !props.isDisabled) { hoverClass += ' hover:-translate-y-2 hover:shadow-[0_15px_30px_rgba(0,0,0,0.8)] cursor-pointer hover:brightness-110'; }
            let transformClass = ''; if (props.isSelected) transformClass = '-translate-y-4 scale-105 z-[100] shadow-[0_0_30px_rgba(218,165,32,0.6)] ring-1 ring-[#daa520]';
            
            return `relative flex flex-col group rounded-[6px] ${sizeClass} ${bgClass} ${hoverClass} ${transformClass}`;
        },
        getMainColorClass: (color, isInverted) => {
            if (isInverted) return 'text-[#d4af37] font-serif tracking-widest drop-shadow-[0_1px_2px_rgba(0,0,0,0.9)]'; 
            if (color === CardColor.RED) return 'text-[#8a1c1c] font-bold opacity-90 mix-blend-multiply'; 
            if (color === CardColor.GREEN) return 'text-[#14532d] font-bold opacity-90 mix-blend-multiply'; 
            return 'text-[#0a0a0a] font-bold opacity-90 mix-blend-multiply';
        },
        getPokerColorClass: (suit, isInverted) => {
            if (isInverted) return 'text-[#d4af37]';
            const isRedSuit = suit === Suit.STRINGS || suit === Suit.TEXTS;
            return isRedSuit ? 'text-[#8a1c1c]' : 'text-[#0a0a0a]';
        },
        getBorderClass: (props) => {
            if (props.isFaceDown) return 'border border-[#3e1010]';
            if (props.isWinner) return 'border-[2px] border-[#ffd700] shadow-[0_0_20px_rgba(255,215,0,0.5)]'; 
            if (props.isSelected) return 'border-[1px] border-[#daa520]';
            return 'border-[0.5px] border-[#3e1010] shadow-[1px_2px_6px_rgba(0,0,0,0.5)]';
        },
        getShadowClass: (props) => {
            if (props.isFaceDown) return 'shadow-[0_5px_15px_rgba(0,0,0,0.9)]';
            return 'shadow-[2px_4px_10px_rgba(0,0,0,0.4)]';
        },
        BackComponent: ImperialCardBack,
        EffectOverlay: ImperialEffectOverlay
    },

    hud: {
        avatarContainerClass: (isMyTurn) => `
            relative w-20 h-20 md:w-24 md:h-24 rounded-full flex items-center justify-center 
            transition-all duration-700 border-[3px] 
            ${isMyTurn 
                ? 'bg-[#150505] border-[#d4af37] shadow-[0_0_40px_rgba(212,175,55,0.4)] scale-105 z-50 ring-2 ring-[#d4af37]/30' 
                : 'bg-[#0a0202] border-[#3e1010] opacity-80'}
        `,
        buttonClass: (disabled) => `
            relative min-w-[150px] h-14 rounded-full flex items-center justify-center transition-all duration-300 font-calligraphy tracking-[0.2em] font-bold text-lg shadow-lg
            ${!disabled 
                ? 'bg-gradient-to-r from-[#2b0a0a] to-[#150505] text-[#d4af37] border border-[#d4af37] hover:bg-[#3e1010] hover:shadow-[0_0_20px_rgba(212,175,55,0.2)]' 
                : 'bg-[#1a1a1a] text-[#444] border border-[#333] cursor-not-allowed'}
        `,
        modalOverlayClass: "fixed inset-0 z-[300] flex items-center justify-center bg-[#0a0202]/95 backdrop-blur-xl p-4 animate-fade-in font-serif",
        modalContentClass: "bg-[#0f0505] border border-[#3e1010] p-8 w-full max-w-5xl rounded-[4px] shadow-[0_0_100px_black] relative overflow-hidden text-[#d4af37] bg-[url('https://www.transparenttextures.com/patterns/black-scales.png')]"
    },

    lighting: {
        StoveLighting: StoveLighting,
        TableBorderFlow: TableBorderFlow
    },

    character: {
        bodyFront: BodyFront,
        bodySideLeft: BodyFront,
        bodySideRight: BodyFront,
        headOutline: '',
        chairComponent: ChairStub
    }
};
