
import { Card, Suit, CardColor, CardRank, PatternDetail, ScoreResult, Player, KaiChongDetail, ViolationRecord, SupportedLanguage, SpecialCaptureFlags } from '../types';
import { ID_WAN_WAN, ID_QIAN_WAN, ID_BAI_WAN, ID_20_WAN, ID_1_GUAN, ID_1_SUO, ID_9_WEN } from '../constants';
import { TRANSLATIONS } from '../constants';
import { evaluateKaiChong } from './kaiChongRules'; 

// --- HELPERS ---
const isRed = (card: Card) => card.color === CardColor.RED;
const isGreen = (card: Card) => card.color === CardColor.GREEN;
const isJinZhang = (card: Card) => isRed(card) || isGreen(card);

// --- PATTERN IDENTIFICATION ---
const identifyPatterns = (cards: Card[]): { details: PatternDetail[], score: number } => {
    const details: PatternDetail[] = [];
    let score = 0;
    // Future expansion: Detect standard patterns
    return { details, score };
};

export const identifyKaiChongSeYang = (uCards: Card[]): { details: PatternDetail[], score: number } => {
    const details: PatternDetail[] = [];
    let score = 0;
    const U = uCards;
    const redCards = U.filter(c => isRed(c));
    const redCount = redCards.length;
    
    // Rule: Ten Red Gathering (Shi Hong Ju Hui)
    if (redCount >= 10) {
        const pts = 1 + (redCount - 10); 
        details.push({ name: `Ten Red Gathering (${redCount})`, score: pts, cards: redCards, category: 'KC_SE_YANG' });
        score += pts;
    }
    
    // Rule: Full House Red (Man Tang Hong)
    if (redCount === U.length && U.length > 8) {
         details.push({ name: `Full House Red`, score: 3, cards: redCards, category: 'KC_SE_YANG' });
         score += 3;
    }

    return { details, score };
};

// --- EXPORTED FUNCTIONS ---

export interface CalculateRoundScoresOptions {
    players: Player[];
    bankerId: number;
    lang: SupportedLanguage;
    kaiChongHistory: KaiChongDetail[];
    violationLog: ViolationRecord[];
    baseScore: number; // Added param
}

export const findBestKaiChongMatch = ({ 
    players, 
    potCard, 
    lang, 
    history, 
    currentAttempterId, 
    specialCaptures 
}: { 
    players: Player[], 
    potCard: Card, 
    lang: SupportedLanguage, 
    history: KaiChongDetail[], 
    currentAttempterId: number, 
    specialCaptures: SpecialCaptureFlags 
}): KaiChongDetail | null => {
    // 1. Try Attempter (Prioritized)
    const attempter = players.find(p => p.id === currentAttempterId);
    if (attempter) {
        const result = evaluateKaiChong(attempter, potCard, history, specialCaptures);
        if (result) {
            return {
                playerId: currentAttempterId,
                matchedCard: potCard,
                sourceCards: result.validCards,
                score: result.score,
                matchType: result.matchType,
                priorityScore: result.priority,
                description: result.description
            };
        }
    }

    // 2. Try Others (Clockwise)
    const order = [1, 2, 3].map(offset => (currentAttempterId + offset) % 4);
    for (const pid of order) {
        const p = players.find(pl => pl.id === pid);
        if (p) {
            const result = evaluateKaiChong(p, potCard, history, specialCaptures);
            if (result) {
                return {
                    playerId: pid,
                    matchedCard: potCard,
                    sourceCards: result.validCards,
                    score: result.score,
                    matchType: result.matchType,
                    priorityScore: result.priority,
                    description: result.description
                };
            }
        }
    }
    
    return null;
};

export const calculateRoundScores = ({
    players,
    bankerId,
    lang,
    kaiChongHistory,
    violationLog,
    baseScore
}: CalculateRoundScoresOptions): ScoreResult[] => {
    
    const CURRENT_BASE = baseScore || 2; // Default fallback

    // 1. Initialize Results Container
    const results = players.map(p => ({
        playerId: p.id,
        trickCount: 0,
        diaoRawValue: 0,
        diaoPointChange: 0,
        kaiZhuPointChange: 0,
        qiaoMenPointChange: 0,
        seYangPointChange: 0,
        kaiChongPointChange: 0,
        kcSeYangPointChange: 0,
        totalRoundChange: 0,
        netScore: 0,
        patternDetails: [] as PatternDetail[],
        kaiChongDetails: [] as KaiChongDetail[],
        cardsWonCount: 0,
        visibleTrickCards: [] as Card[],
        allCapturedCards: [] as Card[],
        violations: violationLog.filter(v => v.playerId === p.id && v.riskLevel === 'PENALTY'), // Only care about PENALTY level
        penaltyReason: undefined as string | undefined,
        patterns: [] as string[],
        scoringCards: [] as Card[]
    }));

    // 2. Data Processing & Counts
    results.forEach(r => {
        const p = players.find(pl => pl.id === r.playerId)!;
        const myKcMatches = kaiChongHistory.filter(h => h.playerId === p.id);
        const potCardIds = new Set(myKcMatches.map(m => m.matchedCard.id));
        const mainCapturedCards = p.capturedCards.filter(c => !potCardIds.has(c.id));
        
        // A. Trick Count
        const mainTricks = p.trickPile.filter(t => t.round <= 8 && !t.isKaiChong).length;
        r.trickCount = mainTricks;
        r.cardsWonCount = mainCapturedCards.length;
        r.allCapturedCards = p.capturedCards;
        r.visibleTrickCards = p.trickPile.filter(t => t.isFaceUp).map(t => t.card);
        
        // Apply Base Multiplier to stored KaiChong Details for display consistency
        // Note: We mutate the score in the detail object for display purposes
        r.kaiChongDetails = myKcMatches.map(m => ({
            ...m,
            score: m.score * CURRENT_BASE
        }));

        // B. Kai Chong Points (Units * Base)
        let kcPoints = 0;
        // Sum the ALREADY MULTIPLIED scores from r.kaiChongDetails
        r.kaiChongDetails.forEach(m => kcPoints += m.score);
        r.kaiChongPointChange = kcPoints;

        // C. Patterns (Units * Base)
        const { details: seYangDetails, score: seYangScore } = identifyPatterns(mainCapturedCards);
        r.seYangPointChange = seYangScore * CURRENT_BASE;
        r.patternDetails.push(...seYangDetails.map(d => ({...d, score: d.score * CURRENT_BASE})));

        const { details: kcSeYangDetails, score: kcSeYangScore } = identifyKaiChongSeYang(p.capturedCards);
        r.kcSeYangPointChange = kcSeYangScore * CURRENT_BASE;
        r.patternDetails.push(...kcSeYangDetails.map(d => ({...d, score: d.score * CURRENT_BASE})));
        
        r.patterns = r.patternDetails.map(pd => pd.name);
        r.scoringCards = p.capturedCards.filter(c => isJinZhang(c));
    });

    // 3. DIAO VALUE LOGIC
    const counts = results.map(r => r.trickCount).sort((a,b) => a - b);
    const isSpecialDistribution = counts.join('') === '0233';

    results.forEach(r => {
        if (r.trickCount < 2) r.diaoRawValue = -1;
        else if (r.trickCount === 2) r.diaoRawValue = 0;
        else r.diaoRawValue = (isSpecialDistribution && r.trickCount === 3) ? 0.5 : 1;
    });

    // 4. PRE-CALCULATE STANDARD SCORES (Before Liability Transfer)
    
    // 4.1 Diao Calculation (Banker vs Peasants)
    const bankerRes = results.find(r => r.playerId === bankerId)!;
    
    results.forEach(r => {
        if (r.playerId !== bankerId) {
            // Formula: (Peasant - Banker) * Base
            const rawScore = (r.diaoRawValue - bankerRes.diaoRawValue) * CURRENT_BASE;
            r.diaoPointChange = rawScore; // Can be positive or negative
        }
    });
    // Banker's Diao score is the inverse sum of all peasants
    const totalPeasantDiao = results.reduce((acc, r) => r.playerId !== bankerId ? acc + r.diaoPointChange : acc, 0);
    bankerRes.diaoPointChange = -totalPeasantDiao;

    // 5. ADD BONUSES (Simplified Additive Model)
    // All components are now pre-multiplied by CURRENT_BASE
    
    results.forEach(r => {
        // Calculate Total Change
        r.totalRoundChange = 
            r.diaoPointChange + 
            r.kaiZhuPointChange + 
            r.qiaoMenPointChange + 
            r.seYangPointChange + 
            r.kaiChongPointChange + 
            r.kcSeYangPointChange;
            
        // Penalty logic could be added here (e.g. if violation, transfer losses)
    });

    return results;
};
